@import url(https://fonts.googleapis.com/css?family=PT+Sans:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.text-area {
  width: 860px;
  margin: 0 auto;
  padding-top: 80px;
  padding-bottom: 200px;
}
.app-body {
  display: flex;
  flex-direction: row;
}

.app-navigation {
  padding: 5px 0px 5px 30px;
  position: relative;
  text-align: left;
  font-family: "Georgia";
  line-height: 2.1;
  text-decoration: none;
  color: #000;
  font-size: 18px;
  width: 150px;
}

.app-password {
  position: absolute;
  width: 100px;
  height: 50px;
  top: 50%;
  left: 50%;
}
.pages-title {
  font-family: "Georgia";
  color: #000;
  font-size: 20px;
}

.pages-line {
  width: 100px;
  border-top: 0.8px solid #aaa;
  margin-top: 10px;
  padding-bottom: 10px;
}

.app-side-bar {
  padding: 40px;
  width: 250px;
  height: auto;
  display: flex;
  flex-direction: column;
}
p {
  margin-bottom: 6.55%;
  position: relative;
  text-align: left;
  font-family: "Georgia";
  line-height: 2.1;
  color: #000;
  font-size: 15px;
  -webkit-font-smoothing: antialiased;
  letter-spacing: 0.1rem;
  padding: 10px 0;
  width: 97.53%;
}
.main_content {
  width: 860px;
  margin-bottom: 100px;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  font-family: "PT Sans";
  font-size: 18px;
}
.tiles {
  padding: 6vh 0vw;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  position: relative;
}
.tile {
  margin: 15px;
  cursor: pointer;
  overflow: hidden;
  width: 18vw;
  height: 18vw;
  img {
    width: 100%;
    -webkit-transition: -webkit-transform 500ms ease;
    transition: -webkit-transform 500ms ease;
    transition: transform 500ms ease;
    transition: transform 500ms ease, -webkit-transform 500ms ease;
    &:hover {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
    }
  }
}

